import React, { useState, useEffect } from 'react'
import { Redirect } from 'react-router-dom'
import {
  CButton,
  CCard,
  CCardBody,
  CCardFooter,
  CCardHeader,
  CCol,
  CCollapse,
  CDropdownItem,
  CDropdownMenu,
  CDropdownToggle,
  CFade,
  CForm,
  CFormGroup,
  CFormText,
  CValidFeedback,
  CInvalidFeedback,
  CTextarea,
  CInput,
  CInputFile,
  CInputCheckbox,
  CInputRadio,
  CInputGroup,
  CInputGroupAppend,
  CInputGroupPrepend,
  CDropdown,
  CInputGroupText,
  CLabel,
  CSelect,
  CRow,
  CWidgetProgress,
  CProgress
} from '@coreui/react'
import CIcon from '@coreui/icons-react'
import { UploadService } from '../../services';
import { httpClient } from '../../helpers';

const Upload = () => {
    const [selectedFiles, setSelectedFiles] = useState(undefined);
    const [selectedClientCode, setClientCode] = useState(undefined);
    const [currentFile, setCurrentFile] = useState(undefined);
    const [progress, setProgress] = useState(0);
    const [message, setMessage] = useState("");
    const [fileInfos, setFileInfos] = useState([]);
    const [redirect, setRedirect] = useState(false);
    const [fetchOptions, setFetchOptions] = useState(true);
    const [selectOptions, setSelectOptions] = useState([{value: "", label: ""}])

    useEffect(() => {
      if(selectedFiles) {
        UploadService.getFiles().then((response) => {
          setFileInfos(response.data);
        });
      }

      if(fetchOptions) {
        const url = "/be/api/upload/clientcode";
        httpClient
          .get(url)
          .then((res) => {
            setSelectOptions(res.data);
          })
          .catch((error) => {
            console.error("Error", error);
          });
          setFetchOptions(false);
      }
    }, [fetchOptions]);

    const selectFile = (event) => {
        setSelectedFiles(event.target.files);
    };

    const renderRedirect = () => {
      console.log("redirect " + redirect);
      if(redirect) {
        return <Redirect to='/' />
      }
    }

    const upload = () => {
        let currentFile = selectedFiles[0];
    
        setProgress(0);
        setCurrentFile(currentFile);
    
        UploadService.upload(currentFile, selectedClientCode, (event) => {
          setProgress(Math.round((100 * event.loaded) / event.total));
        })
          //.then(HandleResponse)
          .then((response) => {
            setMessage(response.data.message);
            setSelectedFiles(undefined);
            return UploadService.getFiles();
          })
          .then((files) => {
            setFileInfos(files.data);
            setRedirect(true);
          })
          .catch(() => {
            setProgress(0);
            setMessage("Could not upload the file!");
            setCurrentFile(undefined);
          });
    
        setSelectedFiles(undefined);
        setProgress(0);
    };

  return (
    <>
      { renderRedirect() }
      <CRow>
        <CCol xs="12" sm="6">
          <CCard>
            <CCardHeader>
              Upload Claim Receipt
            </CCardHeader>
            <CCardBody>
                <CForm action="/api/fileupload" method="post" encType="multipart/form-data" className="form-horizontal">
                <CFormGroup row>
                  <CLabel col md="3" htmlFor="select">Client Code</CLabel>
                  <CCol xs="12" md="9">
                    <CSelect custom name="clientcode" id="clientcode" onChange={event => setClientCode(event.target.value)}>
                    {selectOptions.map((v, i) => {
                      return <option value={v.value} key={v.value}>{v.label}</option>
                    })}
                    </CSelect>
                  </CCol>
                </CFormGroup>
                <CFormGroup row>
                  <CLabel col md="3" htmlFor="file-input">File input</CLabel>
                  <CCol xs="12" md="9">
                    <CInputFile id="file-input" name="file-input" onChange={selectFile}/>
                  </CCol>
                </CFormGroup>
                <CFormGroup row>
                  <CLabel col md="3" htmlFor="file-input"></CLabel>
                  <CCol xs="12" md="9">
                    <CProgress size="sm" color="success" value={progress} className="mb-1 bg-gray" />
                  </CCol>
                </CFormGroup>
                <div className="form-actions">
                    <CButton type="submit" size="sm" color="primary" disabled={!selectedFiles} onClick={upload}><CIcon name="cil-scrubber" /> Submit</CButton>
                </div>
                </CForm>
            </CCardBody>
          </CCard>
        </CCol>
      </CRow>
    </>
  )
}

export default Upload
